.App {
  text-align: center;
}

body {
  font-family:'Courgette', cursive;
    color: #000;
    font: 14px Arial;
    margin: 0 auto;
    padding: 0;
    position: relative;
    background-color: rgba(223, 199, 180, 0.521);
}
.loginImgHome{
  float: left;
  width: 30%;
  height: 30%;
}
.notesContainer {
  text-align: center;
  justify-content:center;
  display:inline-block;
  margin-left: auto;
  margin-right: auto;
  
}
.NotesContainer {
  margin-left: auto;
  margin-right: auto;
  padding-top: 20%;
}
.noteContainer {
  color:#000;
  background:#bfd8d2;
  display: inline-block;  
  height:10em; 
  width:10em; 
  padding:.1em;  
  margin:1em; 
  font-family:Arial, Helvetica, sans-serif;
  -moz-box-shadow:5px 5px 7px rgba(33,33,33,1);
  -webkit-box-shadow: 5px 5px 7px rgba(33,33,33,.7);
  box-shadow: 5px 5px 7px rgba(33,33,33,.7);
  -webkit-transform:rotate(-6deg);
  -o-transform:rotate(-6deg);
  -moz-transform:rotate(-6deg);
  text-decoration:none;
  -moz-transition:-moz-transform .15s linear;
  -o-transition:-o-transform .15s linear;
  -webkit-transition:-webkit-transform .15s linear;
}

.noteContainer:nth-child(even) {
  -o-transform:rotate(4deg);
  -webkit-transform:rotate(4deg);
  -moz-transform:rotate(4deg);
  position:relative;
  top:5px;
  background:#fedcd2;
}

.noteContainer:nth-child(3n) {
  -o-transform:rotate(-3deg);
  -webkit-transform:rotate(-3deg);
  -moz-transform:rotate(-3deg);
  position:relative;
  top:-5px;
 background:#df744a;
}
.noteContainer:nth-child(5n){
  -o-transform:rotate(5deg);
  -webkit-transform:rotate(5deg);
  -moz-transform:rotate(5deg);
  position:relative;
  top:-10px;
  background:#dcb239;
}
.noteContainer:hover,.noteContainer:focus{
  -moz-box-shadow:10px 10px 7px rgba(0,0,0,.7);
  -webkit-box-shadow: 10px 10px 7px rgba(0,0,0,.7);
  box-shadow:10px 10px 7px rgba(0,0,0,.7);
  -webkit-transform: scale(1.25);
  -moz-transform: scale(1.25);
  -o-transform: scale(1.25);
  position:relative;
  z-index:5;
}

.titleNote{
  white-space: nowrap; 
  overflow: hidden;
  text-overflow: ellipsis;
  font-family:'Courgette', cursive;
  font-size:95%; 
  font-weight: bold;  
  padding-bottom:10px; 
  margin-top: 0%;
  margin-bottom: 0%;
}

.messageNote{
  height: 52px;
  max-height: 6.5rem;
  text-align: justify;
  padding: 0 1rem;
  display: block;
  overflow: hidden;
  font-family:'Courgette', cursive;
  font-size:95%;
  margin-top: 0%;
  margin-bottom: 0%;
  text-overflow: ellipsis;
  display: block;
  white-space: nowrap;
}


.textInLogin {
  font-weight:bold;
  font-family:'Courgette', cursive;
